.schedule-table table {
    width: 100%;
    border-collapse: collapse;
}

.schedule-table th,
.schedule-table td {
    border: 1px solid #ccc;
    text-align: center;
    padding: 10px;
}

.schedule-table th {
    background-color: #f8f9fa;
    font-weight: bold;
}

.member-search {
